<template>
<main>
    <ImageModal v-if="modalVisible" @close="modalVisible = false" :imagedata="modalData"/>
    <ChangeModal v-if="modalVisible4" @close4="modalVisible4 = false" :imagedata="modalData4"/>
    <Header :header="content"/>
    <div class="container list  mt-5">
        <table class="table table-hover container" >
            <thead>
                <tr>
                <th scope="col">نام</th>
                <th scope="col">نام خانوادگی</th>
                <th scope="col">نام کاربری</th>
                <th scope="col">سطح دسترسی</th>
                <th scope="col">کد پرسنلی</th>
                <th scope="col">سمت</th>
                <th scope="col">قسمت</th>
                <th scope="col">بخش</th>
                <th scope="col">عکس</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                <td>{{list.name}}</td>
                <td>{{list.lastname}}</td>
                <td>{{list.username}}</td>
                <td>{{list.admin}}</td>
                <td>{{list.code}}</td>
                <td>{{list.who}}</td>
                <td>{{list.div}}</td>
                <td>{{list.section}}</td>
                <td><img  src="../assets/gallery.png" style="widtg:24px;height:24px;cursor:pointer;"  @click="openModal(list)">
                </td>
                </tr>
            </tbody>
        </table>
    </div>
    
    <button class="btn btn-success d-block mx-auto" @click="openModal4">تغییر رمز</button>

</main>
</template>

<script>
import ImageModal from '../components/ImageModal.vue'
import ChangeModal from '../components/ChangeModal.vue'
import Header from '../components/Header.vue'
import axios from 'axios'
export default {
    name:'UserInformation',
    components:{Header,ImageModal,ChangeModal},
    data() {
        return {
            modalVisible: false,
            modalData: null,
            modalVisible4: false,
            modalData4: null,
            content:'اطلاعات کاربری',
            filter:'',
            list:null
        }
    },
    created() {
        axios.get('../my.json')
        .then(({data})=>{
            this.list=data
        })
    },
    methods: {
        
        openModal(msg) {
            this.modalData = msg;
            this.modalVisible = true;
        },
        openModal4(msg){
            this.modalData4 = msg;
            this.modalVisible4 = true;

        }
    
    },
    
      
      
   
    
}
</script>

<style lang="scss" scoped>
.list{
        
        table{
            
            direction: rtl;
            background-color: rgb(255, 255, 255);
            border-radius: 2px;
            text-align: center;
    
                thead{
                    background-color: #082C68;
                    color: rgb(255, 255, 255);
                }
                    
        }
    }
</style>